.menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.menu-container {
  background-color: rgba(255, 255, 255, 0.185);
  border-radius: 30px;
  backdrop-filter: blur(15px);
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.1);
  padding: 40px;
  text-align: center;
  margin-bottom: 20px;
  width: 70%;
}

.genre-title {
  margin-bottom: 30px;
}

.buttons-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.button {
  text-decoration: none;
  color: black;
  width: 200px;
  padding: 20px;
  margin: 10px;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
  cursor: pointer;
}

.button-link {
  color: black;
  text-decoration: none;
}

.button:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 20px rgba(0, 0, 0, 0.1);
}
